<template>
<v-container style="height:100vh;" >
    <v-layout row wrap style="height:60vh">
    </v-layout>
    
    <v-layout row wrap style="height:30vh">
        <v-layout row wrap style="vertical-align-mid">
            <v-flex mb-4 px-5 xs5>
                <v-img :src="getImgUrl('launch_drone.png')" contain style="height:100px;widht:100px" ></v-img>
            </v-flex>
            <v-flex xs7>
                <h1 style="font-size:2.5em; padding-top:30px">이륙하시겠습니까?</h1>
            </v-flex>
        </v-layout>
        
        <v-layout row wrap>
            <div class="slide-wrap center">
                <label >밀어서 이륙</label>
                <input type="range" min="0" max="100" value="0"/>
            </div>
        </v-layout>
    </v-layout>

    <!-- <v-layout row wrap>
        <v-flex xs4>
            <v-img :src="getImgUrl('control_left.png')" contain style="height:100px;widht:100px"></v-img>
        </v-flex> 
        <v-flex xs4>
        </v-flex>  
        <v-flex xs4>
            <v-img :src="getImgUrl('control_right.png')" contain style="height:100px;widht:100px"></v-img>
        </v-flex>    
    </v-layout> -->

    
</v-container>
</template>

<script>
import $ from 'jquery'

export default {
    name:'Intro',
    data(){
        return{
            launch:'false',
            bgImage:'../assets/samle.png'
        }
    },
    mounted(){
        const curSelf = this
        $('.application--wrap').css('background-image',this.getImgUrl('sample2.png'))
        $("input[type=range]").on("change", function(e) {
            var value = $('').val();
        if(value > 0) {
            transValue = 1 - ((value * 2) / 100);
            transValue = transValue <= 0 ? 0 : transValue;
            $("label", $("label").parent()).css({
                opacity: transValue
            });
        }
        }).on("mouseup", function(e) {
        const self = this;
        var value = $(this).val();

        if(value != 100) {
            for(var i = value; i >= 0; i--) {
            (function changeValue(value) {
                setTimeout(function () { 
                $(self).val(value);
                }, (100 - value) * 3);
            })(i);
            }
            $("label", $(self).parent()).animate({
            opacity:1
            }, 500, "linear");
        }else{
            curSelf.goHome();
        }
        });
    },
    methods:{
        getImgUrl(img){
            return require('../assets/'+img)
        },
        goHome(){
            console.log("goHome")
            this.$emit('goHome','')
        }
    }
}
</script>
<style scoped>
#introPage{
    background: url('../assets/sample2.png') center !important;
}
 .container {
    width: 780px;
    margin: 0 auto;
}
.slide-wrap {
  width: 650px;
  position:relative;
}
.center {
  margin: 0 auto;
}
@-webkit-keyframes slidetounlock {
  0% {
  	background-position: -400px 0;
	}
	100%{
		background-position: 200px 0;
	}
}
label {
  width: 650px;
  background: -webkit-gradient(linear,left top,right top,color-stop(0, #4d4d4d),color-stop(0.4, #4d4d4d),color-stop(0.5, white),color-stop(0.6, #4d4d4d),color-stop(1, #4d4d4d)); 
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-animation: slidetounlock 5s infinite;
  color: #4d4d4d;
  font-size: 60px;
  font-family: "HelveticaNeue-Light";
  line-height: 100px;
  height: 100px;
  position: absolute;
	left: 200px;
}
input[type=range] {
  -webkit-appearance: none !important;
  -webkit-border-radius: 30px;
  border-radius: 23px;
  background: -moz-linear-gradient(top, #010101, #181818);
  background: -webkit-gradient(linear,left top,left bottom,color-stop(0, #010101),color-stop(1, #181818));
  border: 2px solid #454545;
  overflow: hidden;
  -webkit-user-select: none;
  width: 650px;
  height: 100px;
}
input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none !important;
    background-image: url('data:image/png;base64,');
  background-position: 0 2px;
  width: 146px;
  height: 98px;
  line-height: 1;
  display: inline-block;
}
</style>